.hero {
  padding-bottom: 5rem;
  @media (min-width: 768px) {
    height: 100vh;
  }

  @media (min-width: 768px) {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    padding: 0 3rem;
    // margin-top: unset;
    // margin-bottom: 4rem;
  }

  .hero__info {
    text-align: center;
    @media (min-width: 768px) {
      flex: 3;
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      text-align: unset;
    }

    h3 {
      color: $green;
      font-size: 4rem;
      font-weight: 300;
      @media (min-width: 768px) {
        font-size: 6rem;
        letter-spacing: 2px;
      }
    }

    h1 {
      font-size: 7rem;
      color: $white;
      font-weight: 600;
      font-family: $secondaryFont;
      @media (min-width: 768px) {
        font-size: 9rem;
        margin: 1rem 0;
        letter-spacing: 2px;
      }
    }

    p {
      font-size: 2rem;
      color: $white;
      font-weight: 100;
      @media (min-width: 768px) {
        font-size: 2.4rem;
      }
    }
  }
  .hero__image {
    text-align: center;
    z-index: 9;
    img {
      width: 60%;
      @media (min-width: 768px) {
        width: 80%;
      }
    }
  }
}
